<template>
  <div class="register-container">
    <!--  -->
    <div class="register">
      <h3>
        
        <span class="go"
          >
          <router-link to="/login"></router-link>
        </span>
      </h3>
      <div class="content">
        <label>:</label>
        <input type="text" placeholder="" v-model="phone" />
      </div>
      <div class="content">
        <label>:</label>
        <input type="text" placeholder="" v-model="code" />
        <button style="height: 38px; margin-left: 1px" @click="getCode">
          
        </button>
      </div>
      <div class="content">
        <label>:</label>
        <input
          type="password"
          placeholder=""
          v-model="password"
        />
      </div>
      <div class="content">
        <label>:</label>
        <input
          type="password"
          placeholder=""
          v-model="password1"
        />
      </div>
      <div class="controls">
        <input name="m1" type="checkbox" v-model="agree" />
        <span></span>
      </div>
      <div class="btn">
        <button @click="userRegister"></button>
      </div>
    </div>

    <!--  -->
    <div class="copyright">
      <ul>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
        <li></li>
      </ul>
      <div class="address"></div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Register",
  data() {
    return {
      phone: "",
      // 
      code: "",
      // 
      password: "",
      // 
      password1: "",
      // 
      agree: true,
    };
  },
  methods: {
    async getCode() {
      try {
        const { phone } = this;
        phone && (await this.$store.dispatch("getCode", this.phone));
        this.code = this.$store.state.user.code;
      } catch (error) {
        alert(error.message);
      }
    },
    async userRegister() {
      try {
        const { phone, code, password, password1 } = this;
        phone &&
          code &&
          password == password1 &&
          (await this.$store.dispatch("UserRegister", {
            phone,
            code,
            password,
          }));
        this.$router.push("/login");
      } catch (error) {
        alert(error.message);
      }
    },
  },
};
</script>

<style lang="less" scoped>
.register-container {
  .register {
    width: 1200px;
    height: 445px;
    border: 1px solid rgb(223, 223, 223);
    margin: 0 auto;

    h3 {
      background: #ececec;
      margin: 0;
      padding: 6px 15px;
      color: #333;
      border-bottom: 1px solid #dfdfdf;
      font-size: 20.04px;
      line-height: 30.06px;

      span {
        font-size: 14px;
        float: right;

        a {
          color: #e1251b;
        }
      }
    }

    div:nth-of-type(1) {
      margin-top: 40px;
    }

    .content {
      padding-left: 390px;
      margin-bottom: 18px;
      position: relative;

      label {
        font-size: 14px;
        width: 96px;
        text-align: right;
        display: inline-block;
      }

      input {
        width: 270px;
        height: 38px;
        padding-left: 8px;
        box-sizing: border-box;
        margin-left: 5px;
        outline: none;
        border: 1px solid #999;
      }

      img {
        vertical-align: sub;
      }

      .error-msg {
        position: absolute;
        top: 100%;
        left: 495px;
        color: red;
      }
    }

    .controls {
      text-align: center;
      position: relative;

      input {
        vertical-align: middle;
      }

      .error-msg {
        position: absolute;
        top: 100%;
        left: 495px;
        color: red;
      }
    }

    .btn {
      text-align: center;
      line-height: 36px;
      margin: 17px 0 0 55px;

      button {
        outline: none;
        width: 270px;
        height: 36px;
        background: #e1251b;
        color: #fff !important;
        display: inline-block;
        font-size: 16px;
      }
    }
  }

  .copyright {
    width: 1200px;
    margin: 0 auto;
    text-align: center;
    line-height: 24px;

    ul {
      li {
        display: inline-block;
        border-right: 1px solid #e4e4e4;
        padding: 0 20px;
        margin: 15px 0;
      }
    }
  }
}
</style>